.autopilot-caller-div {
    width: calc(100% - 10px);
    height: auto; 
    padding: 5px;
    display: inline-block;
}

.header-div h2 {
    margin-left: 5px;
    width: auto;
    font-size: 18px;
    font-weight: 900;
    color: white;
}

.header-div {
    display: inline-block;
    width: 100%;
    height: auto;
}

.header-toolbar-div {
    padding: 5px;
    width: calc(100% - 10px);

}

.autopilot-output {
    height: 100px;
    background-color: #17181d;
    border-radius: 10px;
    color: white;
}

.autopilot-output p {
    color: white;
    padding: 5px;
}

.current-params {
    display: inline-flex;
    width: 100%;
    justify-content: space-evenly;
    color: white;
    padding-top: 5px;
    padding-bottom: 5px;
}