@import url('https://fonts.googleapis.com/css?family=Audiowide');

* {
    margin: 0;
    padding: 0;

}

body {
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    overflow-y: scroll;
    background-color: #0d1019;
    font-family: "Audiowide";
}

.autopilot-page {
    width: 100%;
    height: 100%;
}

.autopilot-body {
    width: 100%;
    height: 90vh;
    display: inline-block;
}

.autopilot-main {
    width: 100%;
    height: 100%;
    display: inline-flex;
}

.autopilot-main-panel {
    width: 100%;
    height: 100%;
    display: inline-block;
}

.autopilot-body .autopilot-toolbar {
    width: calc(100% - 10px);
    min-height: 50px;
    padding: 5px;
    border-bottom: 1px solid white;
    display: inline-flex;
    justify-content: space-evenly;
}

.autopilot-body .autopilot-toolbar .check-input {
    position: relative;
    margin-right: 5px;
    margin: auto;
    width: 15px;
    height: 15px;
}

.autopilot-body .autopilot-toolbar .form-button-div {
    height: auto;
    padding: auto;
    display: inline-flex;
    margin: 5px;
    border: .1vh solid #54565b;
    border-radius: 1vh;
    display: inline-flex;
    justify-content: center;
    padding: 5px;
    width: auto;
}


.autopilot-body .autopilot-toolbar .endzone-toolbar-label {
    margin-right: 10px;
    margin-top: auto;
    margin-bottom: auto;
    color: white;
    font-weight: 100;
    font-size: 18px;
}

.autopilot-body .autopilot-toolbar .toolbar-select-div {
    display: inline-flex;
    margin-top: auto;
    margin-bottom: auto;
    padding-left: 5px;
    padding-right: 5px;
}


.autopilot-main-panel .autopilot-graph-toolbar {
    width: 100%;
    height: auto;
    padding-bottom: 5px;
    border-bottom: 1px solid white;
    display: inline-flex;
}

.autopilot-chart-div .pieChart-div {
    width: calc(33% - 10px);
    margin: 5px;
}

.autopilot-chart-div .bar-chart-container {
    background-color: #262830;
    border: 1px solid #515359;
    border-radius: 5px;
    height: auto;
    margin-top: 5px;
    width: calc(100%);
}


.autopilot-chart-div {
    display: inline-block;
    width: 33%;
    height: 90vh;
}

.autopilot-chart-div .autopilot-gauges {
    display: inline-flex;
    justify-content: space-evenly;
    width: calc(100% - 10px);
    margin-left: 5px;
    margin-right: 5px;
    margin-bottom: 5px;
    background-color: #262830;
    border-bottom: 1px solid #515359;
    border-top: 1px solid #515359;
}

.autopilot-chart-div .autopilot-gauge {
    width: calc(33.333%);
    padding: 5px;
}

.autopilot-sections-container {
    width: calc(100% - 10px);
    display: inline-flex;
    justify-content: space-evenly;
    padding: 5px;
}

.autopilot-map {
    width: 33%;
    margin-top: 5px;
    height: 90vh;
}

.autopilot-manager {
    width: 33%;
    margin-top: 5px;
    height: 90vh;
}

.autopilot-menu {
    width: 100%;
    height: auto;
    background-color: #262830;
}

.autopilot-radar-chart {
    display: inline-flex;
    justify-content: center;
    width: 100%;
    height: 60%;
    background-color: #262830;

}

.autopilot-piechart-container {
    display: inline-flex;
}

.toggle-table {
    width: 50px;
    height: auto;
    position: fixed;
    left: calc(50%);
    bottom: 25px;
    display: inline-flex;
    justify-content: center;
}

.toggle-table:hover {
    transform: scale(1.1);
    cursor: pointer;
}

.table-body {
    position: fixed;
    width: 100%;
    height: 400px;
    left: 0;
    background-color: white;
    bottom: 0px;
}

.table-body .table {
    font-family: "Audiowide";
} 

@media (max-width: 800px) {
    .autopilot-body .autopilot-toolbar{
        max-width: 100%;
        display: inline-block;
        align-items: center;
    }

    .autopilot-body .autopilot-toolbar .toolbar-select-div {
        width: calc(100% - 10px);
        display: inline-flex;
        justify-content: flex-start;
    }

    .autopilot-sections-container {
        display: inline-block;
    }
    .autopilot-manager {
        width: 100%;
    }

    .autopilot-map {
        width: 100%;
    }

    .autopilot-chart-div {
        width: 100%;
    }

    .autopilot-piechart-container {
        display: inline-block;
    }

    .autopilot-chart-div .pieChart-div {
        width: calc(100% - 10px);
    }
}