.bar-graph-div {
    width: 100%;
    height: 100%;
    display: inline-block;
}
.bar-chart {
    display: inline-flex;
    justify-content: center;
    width: 100%;
    height: auto;
}

.bar-chart-controls {
    width: 100%;
    height: auto;

}

.bar-graph-div .select-div {
    display: inline-flex;
    justify-content: space-evenly;
    width: 100%;
}

.bar-graph-div .select{
    padding: 5px;
    width: 33%;
}