@import url('https://fonts.googleapis.com/css?family=Audiowide');

* {
    margin: 0;
    padding: 0;
}



.pie-chart {
    margin: auto;
    width: 100%;
    height: 80%;
    display: inline-flex;
    justify-content: center;
}

.pie-chart-container {
    font-size: 12px;
    margin: auto;
    width: 95%;
    height: 95%;
    padding: 2.5%;
    display: inline-block;
}

.select-div {
    display: inline-flex;
    justify-content: center;
    margin: auto;
    font-size: 12px;
    width: 100%;
}
.box-chart-section {
    width: 100%;
    height: 15vw;
    min-height: 250px;
    background-color: #262830;
    border-radius: 5px;
    border: 1px solid #515359;
    margin-top: 5px;
    display: inline-flex;
}
